.card {
  font-family: "Source Code Pro", monospace;
  background-color: #ffffff;
  padding: 1em;
  border-radius: 5%;
  box-shadow: 1px 2px 3px 0px #8d8d8d;
}

.author {
  color: #8d8d8d;
  margin-bottom: 0.25em;
  font-size: 0.75em;
}

.author strong {
  color: #81b29a;
}

.title {
  margin: 0.25em 0;
  font-size: 1.25em;
  color: #14213d;
  text-transform: uppercase;
}

.date {
  color: #8d8d8d;
}

.votes {
  color: #8d8d8d;
}

.votes__positive {
  color: #00a312;
}

.votes__negative {
  color: #ff5964;
}

.metadata {
  font-size: 0.75em;
  display: flex;
  justify-content: space-around;
  margin-top: 0.75em;
}

.comments {
  color: #8d8d8d;
}

.category {
  color: #14213d;
  margin: 0;
  font-size: 1em;
}

.link {
  text-decoration: none;
  flex-grow: 1;
  width: 300px;
  margin: 0 1em;
}

@media only screen and (min-width: 768px) {
  .card {
    height: 200px;
    font-family: "Source Code Pro", monospace;
    background-color: #ffffff;
    padding: 1em;
    margin: 1em 0;
    border-radius: 5%;
    box-shadow: 1px 2px 3px 0px #8d8d8d;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
  }
}

@media only screen and (min-width: 1280px) {
  .card {
    height: 250px;
  }
}
